.Header__Component {
  background-color: #f01716;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  position: fixed;
  box-sizing: border-box;
  height: 50px;
  top: 0;
  width: 100%;
  padding: 0 50px;

  p {
    font-size: 17px;
    font-style: italic;
    line-height: 1.2;
  }

  h1 {
    margin: 0;
    text-transform: uppercase;
    letter-spacing: -2.2px;
    font-size: 35px;
  }

  .visually-hidden:not(:focus):not(:active) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }

  @media (max-width: 812px) {
    header {
      flex-direction: column;
      padding: 0 25px;

      p {
        margin-top: 0;
      }
    }
  }
}